
.bm {
  height: auto;
  overflow-x: hidden;
  color: #2C3E50;
  font-size: 16px;
  line-height: 27px;
}
.bm h1, .bm h2, .bm h3, .bm h4, .bm h5 {
  font-weight: 500;
}
.bm h3 {
  margin: 20px 0;
}
.bm ul, .bm ol {
  padding: 0;
  margin: 0 0 10px 25px;
}
.bm hr {
  border: 0;
  border-bottom: 1px dashed #cfcfcf;
  margin: 30px 0;
}
.bm .blog-tag {
  display: inline-block;
  margin-right: 10px;
  border: 0;
  background-color: #D6DBDF;
  border-radius: 4px;
  color: #2C3E50;
  font-size: 90%;
  padding: 2px 4px;
  white-space: nowrap;
}
.content img {
  height: auto;
  max-width: 100%;
  vertical-align: middle;
  border: 0;
}



